<template>
  <v-container justify="center">
    <v-flex>
      <v-card class="mx-auto">
        <v-card-title class="justify-center">Buscar Procesos</v-card-title>
        <v-card-text>
          <FormBusqueda
            busEstado=""
            visEstado="1"
            @search="realizarConsulta">
          </FormBusqueda>
        </v-card-text>
      </v-card>
       <br>
      <v-card class="mx-auto">
        <v-card-title class="justify-center">Resultados de la Búsqueda</v-card-title>
        <v-card-text>
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Buscar"
            single-line
            hide-details
          >
          </v-text-field>
          <br>
          <v-data-table
            :headers="headers"
            :items="dataConsulta"
            :search="search"
            class="elevation-1"
          >
            <template v-slot:item.administrar="{ item }">
              <v-icon v-if="$session.get('codigopersonal') == item.ab_asignado"
                class="mr-5"
                color="success"
                @click="itemactual=item, dialog_administrar=true"
              >
                settings_applications
              </v-icon>
              <v-icon v-else
                class="mr-5"
                color="primary"
                @click="itemactual=item, dialog_administrar=true"
              >
                search
              </v-icon>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-flex>
    <v-dialog
      v-model="dialog_administrar"
      persistent
      max-width="1000"
      scrollable
    >
      <v-card elevation="10">
        <v-card-title class="headline justify-center blue--text">
          Documentación
        </v-card-title>
        <v-card-text>
          <v-tabs align-with-title v-model="tab">
            <v-tab key="P">Proceso</v-tab>
            <v-tab key="R">{{ itemactual.referencia + ' - ' + itemactual.titulo }}</v-tab>
            <v-tab key="C" v-if="$session.get('codigopersonal') == itemactual.ab_asignado">Correspondencia</v-tab>
            <v-tab key="N">Novedades al Proceso</v-tab>
          </v-tabs>
          <v-tabs-items
            v-model="tab"
          >
            <v-tab-item key="P">
              <TabProc :itemactual.sync = "itemactual" />
            </v-tab-item>
            <v-tab-item key="R">
               <TabRef :itemactual.sync = "itemactual" />
            </v-tab-item>
            <v-tab-item key="C" v-if="$session.get('codigopersonal') == itemactual.ab_asignado">
               <TabMsg :itemactual.sync = "itemactual" />
            </v-tab-item>
            <v-tab-item key="N">
               <TabNovedades :itemactual.sync = "itemactual" />
            </v-tab-item>
          </v-tabs-items>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" dark text @click="item='',itemactual='',dialog_administrar=false">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import { anunciosProcAdquisicion } from '@/services/api'
import FormBusqueda from '@/components/procesos/FormBusqueda'
export default {
  name: 'principalAdm',
  components: {
    TabProc: () => import('./procesos/procesosTabPro'),
    TabRef: () => import('./procesos/procesosTabRef'),
    TabMsg: () => import('./procesos/procesosTabMsg'),
    TabNovedades: () => import('./procesos/procesosTabNovedades'),
    FormBusqueda
  },
  data () {
    return {
      dataRegistro: [],
      dataConsulta: [],
      search: '',
      itemactual: '',
      ultimaBusqueda: '',
      dialog_administrar: false,
      tab: null,
      headers: [
        { text: 'Título del proceso', align: 'left', value: 'titulo', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Fecha límite', align: 'left', value: 'fecha_cierre_format', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Publicado', align: 'left', value: 'fecha_apertura_format', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Tipo de proceso', align: 'left', value: 'tipo_contratacion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Estado', align: 'left', value: 'estado', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Referencia', align: 'left', value: 'referencia', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Administrar/Ver', align: 'center', value: 'administrar', class: 'subtitle-1 font-weight-bold black--text' }
      ]
    }
  },
  methods: {
    async realizarConsulta (busqueda) {
      this.ultimaBusqueda = busqueda
      this.dataConsulta = []
      if (busqueda.busTitulo === '' && busqueda.busDescripcion === '' && busqueda.busReferencia === '' && busqueda.busFecPubIni === '' && busqueda.busFecLimFin === '' && busqueda.busTipoContratacion === '' && busqueda.busEstado === '') {
        this.$toasted.error('Debe ingresar al menos un criterio de búsqueda', { icon: 'error', duration: 5000 })
      } else {
        try {
          const { data } = await anunciosProcAdquisicion.buscarPropuesta({
            titulo: busqueda.busTitulo,
            descripcion: busqueda.busDescripcion,
            referencia: busqueda.busReferencia,
            fecha_apertura_ini: busqueda.busFecPubIni,
            fecha_apertura_fin: busqueda.busFecPubFin,
            fecha_cierre_ini: busqueda.busFecLimIni,
            fecha_cierre_fin: busqueda.busFecLimFin,
            estado: busqueda.busEstado,
            tipo_contratacion: busqueda.busTipoContratacion,
            tipoadmin: 'ADM'
          })
          this.dataConsulta = data
          console.log(this.dataConsulta)
          if (this.dataConsulta.length === 0) {
            this.$toasted.error('No se encontraron resultados según los criterios de búsqueda', { icon: 'error', duration: 5000 })
          }
        } catch (error) {
          console.log(error)
        } finally {
        }
      }
    }
  }
}
</script>
